@import '../../styles/variables';
@import '../../styles/mixins';

.gallery {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  height: 16rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__button {
    z-index: 2;
    padding: 0.4rem;
    img {
      height: 1.4rem;
    }
  }

  &__numbering {
    display: none;
    font-weight: $font-weight-medium;
    font-size: 1.2rem;
    color: white;
    position: absolute;
    bottom: 1.5rem;
    width: 100%;
    text-align: center;
  }
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;

  &.active {
    opacity: 1;
  }
}

@include media-query-for-screen($tablet) {
  .gallery {
    height: 25rem;
    border-radius: 25px;

    &__button {
      padding: 2rem;
      img {
        height: 4.8rem;
      }
    }

    &__numbering {
      display: inline-block;
    }
  }
}
